  }
}

// Ëé∑ÂèñÁî®Êà∑ÈáëÂ∏Å‰ΩôÈ¢ù
const getUserBalance = async () => {
  try {
    const account = getAccount()
    
    if (!account) {
      userBalance.value = 0
      return
    }
    
    
    const response = await apiGetUserBalance({ account })
    
    if (response.code === 200) {
      userBalance.value = response.data || 0
    } else {
      userBalance.value = 0
    }
  } catch (error) {
    userBalance.value = 0
  }
}

// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
const checkLoginStatus = async () => {
  const token = getToken();
  if (token) {
    try {
      // ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñÁôªÂΩïÊó∂‰øùÂ≠òÁöÑÁî®Êà∑‰ø°ÊÅØ
      const savedUserInfo = uni.getStorageSync('userInfo') || {}
      const loginData = uni.getStorageSync('loginData') || {}
      
      // ‰ºòÂÖà‰ΩøÁî®ÁôªÂΩïÊó∂‰øùÂ≠òÁöÑÂÆåÊï¥Êï∞ÊçÆ
      if (loginData.uname || loginData.account) {
        memberStore.profile = {
          avatar: loginData.himg || savedUserInfo.avatar || 'http://video.caimizm.com/himg/user.png', // ‰ºòÂÖà‰ΩøÁî®himgÔºåÁÑ∂ÂêéÊòØÊú¨Âú∞‰øùÂ≠òÁöÑÂ§¥ÂÉè
          nickname: loginData.uname || savedUserInfo.nickname || 'Ê¨¢ËøéÊÇ®' // uname ÊòØÊòµÁß∞
        };
      } else {
        // Â¶ÇÊûúÊ≤°ÊúâÁôªÂΩïÊï∞ÊçÆÔºå‰ΩøÁî®Êú¨Âú∞Â≠òÂÇ®ÁöÑÁî®Êà∑‰ø°ÊÅØ
