      memberStore.profile = {
        avatar: savedUserInfo.avatar || 'http://video.caimizm.com/himg/user.png',
        nickname: savedUserInfo.nickname || 'Ê¨¢ËøéÊÇ®'
      };
    }
    
    // Ëé∑ÂèñÁî®Êà∑‰ΩôÈ¢ù
    getUserBalance()
  } else {
    // Ê≤°ÊúâtokenË°®Á§∫Êú™ÁôªÂΩï
    memberStore.profile = null;
    userBalance.value = 0
  }
}

// ÈÄÄÂá∫ÁôªÂΩï
const logout = () => {
  uni.showModal({
    title: 'Á°ÆËÆ§ÈÄÄÂá∫',
    content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
